export default {
  name: "vHeader",
  data() {
    return {
      headerRoomName:this.$store.getters.getRoomInfo.mingcheng//
    }
  },
  computed:{
    headerTitle:function () {
      return this.$store.getters.getBusinessInfo.companyName//
    }
  },
  props: ['headerState'],
  methods: {
    goSearchChild() {//homeMenu
      this.$emit('childMethod');
    },
    backHomeMenu(){//homeMenu
      this.$goRoute('homeMenu')
    },
    goOrderInfo(){//
      this.$goRoute('orderInfo')
    }
  }
}



