import vHeader from './home/vHeader'

export default {
  name: "member-cen",
  components: {
    vHeader
  },
  data() {
    return {
      VIPInfo: {},//
      alertShow: false,//alert
      alertInputVal: ''//input
    }
  },
  computed: {
    userInfo: function () {//
      let curInfo = {};
      if (this.$store.getters.getUserInfo.mingcheng) {
        curInfo = this.$store.getters.getUserInfo
      } else {
        curInfo = {
          cardno: 0,
          ye: 0,
          mingcheng: ''
        }
      }
      return curInfo
    }
  },
  methods: {
    queryVIP() {//
      if (this.userInfo.mingcheng === '') {
        this.alertShow = true
      }
    },
    alertCancel() {//
      this.alertShow = false;
      this.alertInputVal=''
    },
    alertConfirm() {//
      if (this.alertInputVal === '') {
        this.$vux.toast.show({
          text: '<span style="white-space:nowrap;"></span>',
          type: 'text'
        })
      } else if (this.alertInputVal.match("^1[34578][0-9]{9}$")) {
        this.$http.post(this.$store.getters.getServerHost + 'getMemberCardInfo', {
            mobile: this.alertInputVal
          }
        ).then((response) => {
          this.$store.commit('setUserInfo', response.body.obj);
          this.alertShow = false
        }).catch(function (response) {
          console.log(response)
        });
      } else {
        this.$vux.toast.show({
          text: '<span style="white-space:nowrap;"></span>',
          type: 'text'
        })
      }
    }
  }
}



