import goodItem from './home/goodItem'

export default {
  name: 'search',
  data() {
    return {
      title: 'search',
      value: '',//input
      searchMenuGood: [],//
      searchVal: []//
    }
  },
  components: {
    goodItem
  },
  mounted: function () {
    this.$refs.searchInput.setFocus();//input
    this.searchMenuGood = this.$route.params.menuGood;//
    this.$refs.searchList.style.height = window.innerHeight - 91 + 'px'//
  },
  methods: {
    getResult(val) {//
      let _that = this;
      this.value = val;
      this.searchVal = [];
      this.searchMenuGood.forEach(function (p) {
        if (p.pinyin.indexOf(val) != -1&&val!='') {
          _that.searchVal.push(p)
        }
      })
    },
    onCancel() {//
      this.$goRoute('homeMenu')
    },
    goodCountChange(item) {//homeMenu
      this.$parent.goodCountChange(item)
    }
  }
}



